import Vue from 'vue';
import i18n from '../../../i18n';
import XPostForm from './post-form.vue';
export default Vue.extend({
    i18n: i18n('desktop/views/components/post-form-window.vue'),
    components: {
        XPostForm
    },
    props: {
        reply: {
            type: Object,
            required: false
        },
        mention: {
            type: Object,
            required: false
        },
        animation: {
            type: Boolean,
            required: false,
            default: true
        },
        initialText: {
            type: String,
            required: false
        },
        initialNote: {
            type: Object,
            required: false
        },
        instant: {
            type: Boolean,
            required: false,
            default: false
        },
    },
    data() {
        return {
            uploadings: [],
            files: [],
            geo: null
        };
    },
    computed: {
        maxHeight() {
            return window.innerHeight - 50;
        },
    },
    mounted() {
        this.$nextTick(() => {
            this.$refs.form.focus();
        });
    },
    methods: {
        onChangeUploadings(files) {
            this.uploadings = files;
        },
        onChangeFiles(files) {
            this.files = files;
        },
        onGeoAttached(geo) {
            this.geo = geo;
        },
        onGeoDettached() {
            this.geo = null;
        },
        onPosted() {
            this.$refs.window.close();
        },
        onWindowClosed() {
            this.$emit('closed');
            this.destroyDom();
        }
    }
});
